import BScroll from "better-scroll";
import { categoryList } from "./../home/js/nav-category";
export default {
  components: { categoryList },
  data() {
    return {
      scrollY: 0, // 定义的Y滚动轴及初始值
      categoryHeight: "", // 控件高度
      rightLiTops: [], //所有分类头部位置
      categoryTypeList: [], // 菜单集合
      isLeftItemClick: false // 是否点击了左边item
    };
  },
  watch: {
    categoryTypeList() {
      //监听数据
      this.$nextTick(() => {
        //左右两边滚动
        this.initScroll();
        //右边列表高度
        this.initRightHeight();
      });
    }
  },
  computed: {
    //动态绑定class类名
    currentIndex(index) {
      const { scrollY, rightLiTops } = this;
      return rightLiTops.findIndex((tops, index) => {
        this.initleftScroll(index); //调用左右联调滚动效果
        return scrollY >= tops && scrollY < rightLiTops[index + 1];
      });
    }
  },
  mounted() {
    // 动态控制分类视图的高度
    this.categoryHeight = "height:" + (window.innerHeight - 55 - 53) + "px";
    // 读取缓存数据
    if (this.$store.state.categoryStore.categoryList.length > 0) {
      this.categoryTypeList = this.$store.state.categoryStore.categoryList;
    } else {
      var list = categoryList.splice(1, categoryList.length);
      this.$store.dispatch("categoryStore/saveCategoryList", list);
      this.categoryTypeList = list;
    }
  },
  beforeDestroy() {
    this.leftScroll.destroy();
    this.rightScroll.destroy();
  },
  methods: {
    initScroll() {
      var that = this;
      this.leftScroll = new BScroll(this.$refs.leftScroll, {
        click: true,
        probeType: 3
      });
      this.rightScroll = new BScroll(this.$refs.rightScroll, {
        click: true,
        bounce: false,
        probeType: 3
      });
      this.leftScroll.on("scroll", pos => {
        console.log(pos);
      });
      this.rightScroll.on("scroll", pos => {
        this.scrollY = Math.abs(pos.y);
      });
    },
    // 左右联调
    initleftScroll(index) {
      let menu = this.$refs.leftScroll;
      let el = menu[index];
      this.leftScroll.scrollToElement(el, 300, 0, -300);
    },
    // 求出右边列表的高度
    initRightHeight() {
      let itemArray = []; //定义一个伪数组
      let top = 0;
      itemArray.push(top);
      // 获取右边所有li的
      let allList = this.$refs.rightScroll.getElementsByClassName("rightItem");
      //allList伪数组转化成真数组
      Array.prototype.slice.call(allList).forEach(item => {
        top += item.clientHeight; //获取所有li的每一个高度
        itemArray.push(top);
      });
      this.rightLiTops = itemArray;
      console.log("左边item高度集合", this.rightLiTops);
    },
    // 左边item点击事件
    leftItemClick(index, item) {
      console.log(item, this.rightLiTops[index]);
      this.rightScroll.scrollTo(0, -this.rightLiTops[index]);
    }
  }
};



