
import Home from "../home/home";
import Category from "../category/category";
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
name: "index",
components: { Home, Category, Tabbar, TabbarItem },
data() {
return {
tabbarIndex: 0
};
},
computed: {},
watch: {},

methods: {
searchClick(){
this.$router.push({name:"search"});
},
tabbarItemClick(tabbarIndex) {
this.tabbarIndex = tabbarIndex;
}
},
created() {},
mounted() {}
};



